// add fonts for the site here

@font-face {
  font-family: 'Fira Sans Regular';
  font-weight: 400;
  font-style: normal;
  src: url('../fonts/Fira-Sans-regular/Fira-Sans-regular.eot');
  src: url('../fonts/Fira-Sans-regular/Fira-Sans-regular.eot?#iefix') format('embedded-opentype'),
       local('Fira Sans Regular'),
       local('Fira-Sans-regular'),
       url('../fonts/Fira-Sans-regular/Fira-Sans-regular.woff2') format('woff2'),
       url('../fonts/Fira-Sans-regular/Fira-Sans-regular.woff') format('woff'),
       url('../fonts/Fira-Sans-regular/Fira-Sans-regular.ttf') format('truetype'),
       url('../fonts/Fira-Sans-regular/Fira-Sans-regular.svg#FiraSans') format('svg');
}

